export const listAllSubmit = async (event_id)=>{
  const id = event_id;
  const db = app.firestore();
  if(typeof id !=="undefined"){
    const querySnapshot = await db.collection('live_events').doc(id).collection('submission').get();
    let listSubmit = await querySnapshot.docs.map(async(doc) => {
      // let listSubmit = [];
      // for await (let doc of querySnapshot.docs) {
      const submissionData = doc.data();
      let result = {};
      if(submissionData.status === 2){
        const studentRef =  db.collection('students').doc(submissionData.uid);
        const studentData = await studentRef.get();
        if (!studentData.exists) {
          const userRef = db.collection('users').doc(submissionData.uid);
          const userData = await userRef.get();
          result.display_name = typeof (userData.data().display_name)!=="undefined"?userData.data().display_name:submissionData.display_name;
          result.school_id = typeof(submissionData.school_id)!=="undefined"?submissionData.school_id:userData.data().school_id;
          result.point = submissionData.point;
          result.submit_at = convertTimestamp(submissionData.submit_at);
          result.created_at = convertTimestamp(submissionData.created_at);
          result.time_work =parseInt((submissionData.submit_at - submissionData.created_at)/60);
          result.email = typeof(userData.data().email)!=="undefined"?userData.data().email:'';
          result.grade = typeof (userData.data().class)!=="undefined" ?userData.data().class:'';
          if(parseInt((submissionData.submit_at - submissionData.created_at)/60)>70){
            result.autosubmit = "Tool auto";
          }else{
            result.autosubmit = '';
          }
          result.not_student ="Chưa có trong Student";
        } else {
          result.display_name = typeof (submissionData.display_name)!=="undefined"?submissionData.display_name:studentData.data().lastname+studentData.data().firstname;
          result.school_id = typeof(submissionData.school_id)!=="undefined"?submissionData.school_id:studentData.data().school_id;
          result.point = submissionData.point;
          result.submit_at =  convertTimestamp(submissionData.submit_at);
          result.created_at = convertTimestamp(submissionData.created_at);
          result.time_work =parseInt((submissionData.submit_at - submissionData.created_at)/60);
          result.email = typeof(studentData.data().email)!=="undefined"?studentData.data().email:'';
          result.grade = typeof (studentData.data().class)!=="undefined" ?studentData.data().class.name:'';
          if(parseInt((submissionData.submit_at - submissionData.created_at)/60)>70){
            result.autosubmit = "Tool auto";
          }else{
            result.autosubmit = '';
          }
          result.not_student = '';
        }
        console.log(result);
        listSubmit.push(result);
      }
    });
    console.log(listSubmit);
    return listSubmit;
  }
};

