export const addEvent = async (values, image_full) => {
  if(!('start_at' in values)){
    values.start_at = firebase.firestore.FieldValue.serverTimestamp();
  }
  if(!('end_at' in values)){
    values.end_at = firebase.firestore.FieldValue.serverTimestamp();
  }

  if(typeof values.school !==null && typeof values.school.id!=='undefined'){
    let quizRef = await app.firestore().collection('resource/kimchung/quizzes/').doc(values.quizid);
    values.quizRef=quizRef;
  }else{
    let quizRef = await app.firestore().collection('resource/public/quizzes/').doc(values.quizid);
    values.quizRef=quizRef;
  }

  var metadata = {
    eventType: 'image/jpeg'
  };
  try {
    values.created_at=Date.now();
    if(typeof values.start_at==null){
      values.start_at=Date.now();
    }
    if(typeof values.end_at==null){
      values.end_at=Date.now();
    }
    let detail = ' <news content-type=\'news\'>' + values.detail + '</news>';
    delete values.detail;
    values.created_at = firebase.firestore.FieldValue.serverTimestamp();
    let arr = values.tags;
    arr = arr.filter(item => item);
    values.tags = arr;
    let eventRef = await app.firestore().collection('live_events').add(values);

    let event = await eventRef.get();
    console.log('done event:' + event.id);
    eventRef.collection('detail').doc('content').set({ data: detail });
    let eventData = event.data();

    if (typeof (image_full) == 'undefined') {
      return;
    }

    const tool = await fromImage(image_full);
    const image = await tool.thumbnail(512).toFile();
    let cover_url = 'public/images/events/' + event.id;
    let cover_full = 'public/images/events/full/' + event.id;
    var storageRef = app.storage().ref();
    var uploadTask = storageRef.child(cover_full).put(image_full, metadata);
    uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED, // or 'state_changed'
      function(snapshot) {
        // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
        var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        console.log('Upload cover is ' + progress + '% done');
        switch (snapshot.state) {
          case firebase.storage.TaskState.PAUSED: // or 'paused'
            console.log('Upload cover is paused');
            break;
          case firebase.storage.TaskState.RUNNING: // or 'running'
            console.log('Upload cover is running');
            break;
        }
      },
      function(error) {

        switch (error.code) {
          case 'storage/unauthorized':
            console.log('unauthorized');
            break;
          case 'storage/canceled':
            console.log('cancel');
            break;
          case 'storage/unknown':

            console.log('unkonwn');
            // Unknown error occurred, inspect error.serverResponse
            break;
        }
      },
      function() {
        // Upload completed successfully, now we can get the download URL
        uploadTask.snapshot.ref.getDownloadURL().then(function(downloadURL) {
          console.log('image full available at', downloadURL);
          eventData.cover_full = downloadURL;
          app.firestore().collection('live_events').doc(event.id).update(eventData);
        });
      });
      if (typeof (image) == 'undefined') {
        return;
      }
      var uploadTask2 = storageRef.child(cover_url).put(image, metadata);
      uploadTask2.on(firebase.storage.TaskEvent.STATE_CHANGED, // or 'state_changed'
        function(snapshot) {
          // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
          var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
          console.log('Upload cover is ' + progress + '% done');
          switch (snapshot.state) {
            case firebase.storage.TaskState.PAUSED: // or 'paused'
              console.log('Upload cover is paused');
              break;
            case firebase.storage.TaskState.RUNNING: // or 'running'
              console.log('Upload cover is running');
              break;
          }
        },
        function(error) {

          switch (error.code) {
            case 'storage/unauthorized':
              console.log('unauthorized');
              break;
            case 'storage/canceled':
              console.log('cancel');
              break;
            case 'storage/unknown':

              console.log('unkonwn');
              // Unknown error occurred, inspect error.serverResponse
              break;
          }
        },
        function() {
          // Upload completed successfully, now we can get the download URL
          uploadTask2.snapshot.ref.getDownloadURL().then(function(downloadURL) {
            console.log('image available at', downloadURL);
            app.firestore().collection('live_events').doc(event.id).update({cover:downloadURL});
          });
        });
  } catch (err) {
    console.log(err);
  }
};

export const deleteEvents = (ids) => {
  try {
    var storageRef = app.storage().ref();
    ids.map((id) => {
      console.log(id);
      let cover_url = 'public/images/events/' + id;
      let cover_full = 'public/images/events/full' + id;

      storageRef.child(cover_url).getDownloadURL().then(onResolve, onReject);

      function onResolve(foundURL) {
        storageRef.child(cover_url).delete();
      }

      function onReject(error) {
        console.log(error.code);
      }
      storageRef.child(cover_full).getDownloadURL().then(onResolve2, onReject2);

      function onResolve2(foundURL) {
        storageRef.child(cover_full).delete();
      }

      function onReject2(error) {
        console.log(error.code);
      }
      app.firestore().collection('events').doc(id).collection('detail').doc('content').delete();
      app.firestore().collection('events').doc(id).delete();
    });
  } catch (err) {
    console.log(err);
  }
};

