export const autoSubmitEvent = async function (eventId) {
console.log(eventId);
  let eventid = eventId;
  if(eventid==null||eventid==""){
    return {
      error_code:1,
      message:'không tồn tại eventId'
    };
  }
  const db =  app.firestore();
  let correct_count = 0;
  const liveRef = await db.collection('live_events').doc(eventid).get();
  const liveData = await liveRef.data();
  let total=0;
  if(liveData!=null){
    let quizRef = await liveData.quizRef.get();
    const quizData = await quizRef.data();
    const questions = quizData.questions;
    for await (let question of questions) {
      if("id" in question){
        total++;
      }
    }
  }
  const snapshot = await db.collection('live_events').doc(eventid).collection('submission').where("status","==",1).limit(50).get();
  for await (let submissionDoc of snapshot.docs) {
    console.log("cuongpt");
    console.log(submissionDoc);
    try{
      const attemptsCollectionSnapshot = await submissionDoc.ref.collection("attemps").get();
      for await (let anAttempt of attemptsCollectionSnapshot.docs) {
        const myattempt = anAttempt.data();
        if("selection" in myattempt)
          for await (let aSelection of myattempt.selection) {
            if (aSelection != null)
            {
              const selectedValue = aSelection.selection_values[0];
              const snapshotX = await aSelection.quiz_ref.get();
              if (snapshotX.exists)
              {
                const questionData = snapshotX.data();
                for await (let anAnswer of questionData.options) {
                  if (anAnswer.fraction>0){
                    if (anAnswer.value==selectedValue)
                    {
                      correct_count++;
                      //console.log("Right answer: ,", anAnswer.text,",",anAnswer.value,",", selectedValue,",",doc.ref.path,",",doc2.ref.path,",",correct_count,",",myattempt.display_name);
                      console.log(correct_count,",",submissionDoc.id,snapshotX.data().label);
                    }
                  }
                };
              }
            }
          };
        // console.log("Graded: ,",doc.ref.path,",",correct_count,",",myattempt.display_name);
      };
      const point = Math.round(correct_count*1000/total)/100;
      let update = {
        correct_quiz_count:correct_count,
        grade:correct_count,
        grade_max:total,
        total_quiz_count:total,
        point:point,
        status:2,
        submit_at:await firebase.firestore.FieldValue.serverTimestamp(),
        submit_by:"auto"

      };
      await submissionDoc.ref.update(update);
      // await db.collection('live_events').doc(eventid).collection('submission').doc(submissionDoc.id).update(update);
      console.log("Graded: ,",submissionDoc.id,",",update,",",submissionDoc.id);
      correct_count=0;
      return {
        error_code:0,
        message:'Tất cả các bài thi đã được tự động nộp'
      };
    } catch (e) {
      console.log(submissionDoc.id);
      console.log( e);
      return {
        error_code:1,
        message:'không tồn tại eventId'
      };
    }
  };
  return {
    error_code:0,
    message:'Tất cả các bài thi đã được tự động nộp'
  };
};


