function convertTimestamp(timestamp) {
  if(timestamp){
    let date = timestamp.toDate();
    let mm = date.getMonth();
    let dd = date.getDate();
    let yyyy = date.getFullYear();
    let hh = date.getHours();
    let mn = date.getMinutes();
    let ss = date.getSeconds();

    date = dd +'/'+ mm + '/' + yyyy + ' '+ hh + ':'+mn+':'+ss;
    return date;
  }else{
    return null;
  }
}

